.header {
    color: #fff;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60vh;
    .nav-bg {
        width: 100%;
        padding-bottom:10px;
        background: linear-gradient(rgba(0,0,0,0.6) , rgba(0,0,0,0));
        border-top: 4px solid red;
        position: fixed;
        top: 0;
        left: 0;
        .header-nav{
            display: grid;
            grid-template-columns: 150px 1fr 150px 20px;
            justify-items: center;
            align-items: center;
            padding-top: 20px;
            a{
                font-size: 0.9em;
                text-decoration: none;
                color: #fff;
                &:hover{
                    color: red;
                }
            }
            .c-nav{
                width: 100%;
                display: flex;
                justify-content: space-evenly;
            }
        }
    }
    .header-txt{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h1{
            margin-top: 80px;
            font-size: 2.5em;
            letter-spacing: 6px;
        }
    }
}
.hero {
    background: url(../img/master-slides-03.jpg) center/cover;
    height: 100vh;
    .header-txt{
        p{
            margin: 0;
            margin-top: 80px;
            font-size: 3em;
            letter-spacing: 2;
            text-transform: none;
        }
        h1{
            font-size: 6em;
        }
    }
}
.blog {
    background: url(../img/master-slides-05.jpg) center/cover;
}
.contact {
    background: url(../img/master-slides-06.jpg) center/cover;
}
